.content {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.home{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.bg-blue {
    background: var(--dynamic_blue);
}

.bg-gray {
    background: var(--panel_tab_active_background);
}

.btn-white {
    margin: 10px;
    background: #FFFFFF;
    border-radius: 5px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #000000;
}

.btn-back {
    border: none;
    background: transparent;
    color: #fff;
}

.btn-primary {
    margin: 10px;
    background: #1792DA;
    border-radius: 5px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #fff;
    border-color: #ffffff42;
}

.gallery {
    height: auto;
}

.panel {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.PanelHeader{
    display: none;
}
.title {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
    text-align: center;
    color: #FFFFFF;
}

.text {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
}

.arrow {
    margin: 20px;
}

.header {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #1792DA;
}

.header-title {
    margin: auto;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    color: #FFFFFF;
}

.slider-wrap {
    display: flex;
}

.slider-gallery {
    flex: 1;
    display: block;
    width: 100%;
    min-width: 360px;
}

.slider-btn {
    width: auto;
    padding: 30px;
    color: #fff;
}

.product {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.img-product {
    max-height: 400px;
}

.error {
    color: red;
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-image: url("./img/bg.svg");
    background-repeat: no-repeat;
    background-position: bottom;
}